.xd__tag {
  display: inline-block;
  line-height: 1.2;
  cursor: pointer;
  color: #1f2d3d;
  -webkit-appearance: none;
  box-sizing: border-box;
  text-align: center;
  white-space: nowrap;
  font-size: 12px;
  margin: 0px;
  border-radius: 22px;
  padding: 3px 8px;
}
.xd__tag--close {
  font-size: 1.2rem;
  vertical-align: middle;
}
.xd__tag.is-round {
  border-radius: 100px;
}
.xd__tag--default {
  color: #515a6e;
  background: #fff;
  border: 1px solid #dcdee2;
}
.xd__tag--success {
  color: #fff;
  background: #19be6b;
  border: 1px solid #19be6b;
}
